.taskInputBox {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    border-bottom: 2px solid;
    border-color: #f0f0f0;
}

.taskInput {
    color: #696969;
    outline: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    border: 0px solid;
    width: calc(100% - 85px);
    height: 38px;
    margin-left: 15px;
    margin-bottom: 10px;
}

.taskInput::placeholder {
    opacity: 0.5;
}

.buttonAddTask {
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    color: #696969;
    border-radius: 100%;
    margin-right: 15px;
    margin-left: auto;
    border: none;
    width: 40px;
    height: 40px;
    margin-bottom: 10px;
    transition-duration: 0.4s;
}

.buttonAddTask:hover {
    background-color: #996fc9;
    color: white;
}

.buttonAddTask:disabled,
.buttonAddTask[disabled] {
    visibility: hidden;
    color: white;
}